<template>
  <div id="my_score">
    <Sidebar @setTable="set_table" @setDate="set_date" :model="model"/>
    <div class="main" id="page-wrap">
      <div>
        <h2 style="display:inline"></h2>
        <a :href="model.get_twitter_link()" target="_blank" v-if="model.song_is_set()">
          <img src="../assets/twitter.png" alt="Twitter" width="40" height="40">
        </a>
      </div>
      <Viewer v-if="model.song_is_set()" :model="model"/>
      <p v-else>{{ message }}</p>
    </div>
  </div>
</template>

<script>
import Viewer from "./viewer/Viewer";
import Sidebar from "./viewer/Sidebar";
import Model from "../models/model";

export default {
  name: "MyScore",
  components: {Viewer, Sidebar},
  data: () => ({
    model: Model.default(),
    message: ""
  }),
  async beforeMount() {
    this.model = await this.model.init_table(this.$store.getters.accessToken)
    await this.fetch_detail()
  },
  methods: {
    async fetch_detail() {
      this.message = "...";
      this.model = await this.model.init_my_score(
          this.$store.getters.accessToken,
      )
      this.message = this.model.song_is_set() ? "" : "";
    },
    /**
     * @public
     * @param {Date} date
     */
    async set_date(date) {
      this.model = this.model.set_date(date)
      await this.fetch_detail()
    },
    /**
     * @param {string} table
     */
    set_table(table) {
      this.model = this.model.set_table(table)
    },
  },
  watch: {
    date: {
      async handler() {
        await this.fetch_detail()
      }
    }
  }
}
</script>

<style scoped>
#page-wrap {
  flex-direction: column; /*   =>  */
  min-height: 100vh; /*  vhviewport() height */
  min-width: 700px;
  max-width: 700px;
  padding-top: 20px;
  margin-left: calc(max(0px, calc(100% - 1060px)) / 2 + 340px); /*  */
  margin-right: calc(max(0px, calc(100% - 1060px)) / 2 + 20px);
}
</style>