<template>
  <div id="profile-edit">
    <h1></h1>
    <label>
      :
      <input v-model="name">
    </label>
    <button class="btn btn-success" @click="change_name"></button>
    <br />
    {{ message }}
  </div>
</template>

<script>
import Api from "../api.js"

export default {
  name: "ProfileEdit",
  data: () => ({
    name: "",
    message: "",
  }),
  mounted() {
    this.name = this.$store.getters.userInfo.user_name;
  },
  methods: {
    async change_name() {
      if (this.name === "" || this.name === this.$store.getters.userInfo.user_name) {
        return;
      }
      let res = await Api.change_user_name(this.$cookies.get('session-token'), this.name);
      this.$store.commit("setUserInfo", res);
      this.message = "" + this.name + ""
    }
  }
}
</script>

<style scoped>
#profile-edit {
  padding-top: 20px;
}
</style>