<template>
  <div class="sidebar">
    <TableSelector :model="model" @setTable="set_table" class="header-item" v-if="model.tables_is_set ()"/>
    <DateSelector @setDate="set_date" class="header-item"/>
    <FilterPreset :model="model" class="header-item"/>
    <SongFilter :model="model" class="header-item"/>
    <DetailColumns :model="model" class="header-item"/>
  </div>
</template>

<script>
import TableSelector from "./TableSelector";
import DateSelector from "./DateSelector";
import SongFilter from "./SongFilter";
import DetailColumns from "./DetailColumns";
import FilterPreset from "./FilterPreset";
import Model from "../../models/model";

export default {
  name: "Sidebar",
  components: {DateSelector, DetailColumns, TableSelector, SongFilter, FilterPreset},
  props: {
    model: {
      type: Model,
      required: true,
    },
  },
  methods: {
    /**
     * @param {string} table
     */
    set_table(table) {
      this.$emit('setTable', table);
    },
    set_date(date) {
      this.$emit('setDate', date);
    },
  }
}

</script>

<style scoped>
.sidebar {
  background-color: #191970;
  color: #ffffff;
  height: 100%; /*  */
  width: 320px; /* width */
  max-width: 320px; /* width */
  opacity: 0.90; /*  0 */
  position: fixed; /* () */
  overflow-x: hidden; /*  */
  overflow-y: scroll; /*  */
  box-sizing: border-box; /* paddingborderwidthheight */
  padding-top: 20px;
  padding-bottom: 60px;
}

.header-item {
  padding-right: 20px;
}

/deep/ .sidebar-title {
  margin-left: 20px;
  text-align: left;
}

/deep/ .sidebar-body {
  margin-left: 30px;
  text-align: left;
}

</style>