<template>
  <div id="song_filter">
    <h6 class="sidebar-title" @click="filter_visible">{{ show ? "" : "" }}</h6>
    <transition>
      <div v-show="show" class="sidebar-body">
        <div class="filter">
          <h5></h5>
          <div v-for="lamp in config().LAMP_TYPE" :key="lamp">
            <label :for="lamp" class="sidebar-list">
              <input type="checkbox" :id="lamp" :value="lamp" v-model="model.filter.visible_lamp[lamp]">
              {{ lamp }}
            </label>
          </div>
          <button class="btn btn-danger" @click="model.filter.visible_reverse()"></button>
          <button class="btn btn-success" @click="model.filter.visible_all_lamp_type()"></button>
          <button class="btn btn-success" @click="model.filter.visible_not_full_combo()">FC</button>
          <button class="btn btn-success" @click="model.filter.visible_not_ex_hard()">EXH</button>
          <button class="btn btn-success" @click="model.filter.visible_not_hard()">HARD</button>
          <button class="btn btn-success" @click="model.filter.visible_not_easy()">EASY</button>
        </div>

        <div class="filter">
          <h5></h5>
          <div v-for="rank in config().RANK_TYPE" :key="rank">
            <label :for="rank">
              <input type="checkbox" :id="rank" :value="rank" v-model="model.filter.visible_rank[rank]">
              {{ rank }}
            </label>
          </div>
        </div>

        <div class="filter">
          <h5></h5>
          <button class="btn btn-success" @click="model.filter.filter_all_term()"></button>
          <button class="btn btn-success" @click="model.filter.filter_older_half_year()"></button>
          <button class="btn btn-success" @click="model.filter.filter_older_one_year()">1</button>
          <button class="btn btn-success" @click="model.filter.filter_older_two_year()">2</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import config from "../../const";
import Model from "../../models/model";

export default {
  name: "SongFilter",
  props: {
    model: {
      type: Model,
      required: true,
    }
  },
  data: () => ({
    show: false,
  }),
  methods: {
    config() {
      return config;
    },
    filter_visible() {
      this.show = !this.show;
    },
  },
}
</script>

<style scoped>
.filter button {
  margin: 5px;
  display: inline
}

.sidebar-list {
  margin-left: 10px;
}
</style>